* {
  font-family: "Space Grotesk", sans-serif;
}

.nav {
  margin-left: 100px;
  margin-right: 150px;
}

.nav-link {
  font-family: "Karla", sans-serif;
}

.nav-top {
  background-color: white;
}

html,
body {
  max-width: auto;
  overflow-x: hidden;
}

.intro-color {
  color: lightseagreen;
}

.lead {
  color: #a2a0b8;
}

.jumbotron {
  background-color: #e9f8ff !important;
  width: 80% !important;
  border-radius: 30px !important;
}

.case-study {
  background-color: #fff4f6;
}

.navbar-right {
  float: right !important;
}

.navbar-bottom {
  display: block;
}

.cover {
  object-fit: cover;
  width: 100%;
  height: 300px;
}

.col-md-4 {
  margin-bottom: 20px;
}

h1.display-5 {
  text-align: center;
  font-size: 2.5rem;
  font-weight: 500;
}

#contact {
  padding: 4rem 2rem;
  width: 100%;
}

footer {
  padding: 100px;
}
